import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormArray, FormControl } from '@angular/forms';
import { PyequionService } from '../pyequion.service';
import { Observable, BehaviorSubject } from 'rxjs';
import { map, filter } from 'rxjs/operators';
import { PyequionStoreService } from '../pyequion-store.service';

const pCO2_ref = 0.0003908408957924021
const mapCloseEquationEnum = {
  'OPEN': 0,
  'CARBON_TOTAL': 1,
  'PH': 2,
  'NONE': 3,
}
const mapCloseEquationInverse = {
  0: 'OPEN',
  1: 'CARBON_TOTAL',
  2: 'PH',
  3: 'NONE',
}

const mapTypeActivityCalculation = {
  'IDEAL': 0,
  'DEBYE': 1,
  'DEBYE_MEAN': 2,
  'PITZER': 3,
}

@Component({
  selector: 'app-stepper',
  templateUrl: './stepper.component.html',
  styleUrls: ['./stepper.component.scss']
})
export class StepperComponent implements OnInit {
  objectKeys = Object.keys

  // private eqSystemSubject = new BehaviorSubject<EquilibriumModel>(null);
  // public eqSystem$ = this.eqSystemSubject.asObservable();

  NONE = mapCloseEquationEnum['NONE']
  isLinear = false;
  firstFormGroup: FormGroup;
  equilibriumCalcForm: FormGroup;
  compoundsInputForm: FormGroup
  indexSpeciesShow: number[]
  // reactions$: Observable<any[]>

  // readonly NONE_VAL = 3

  closingEquationTypes = [
    {label: 'Open', value: 0},
    {label: 'Closed', value: 1},
    {label: 'pH', value: 2},
    {label: 'None', value: mapCloseEquationEnum['NONE']},
  ]

  mapCloseEquationLabel = {
    0: 'CO2 Pressure (atm)',
    1: 'Total Carbone (mM??)',
    2: 'pH',
    3: null,
  }

  nonIdealitiesTypes = [
    {label: 'Debye-Huckel (B-dot)', value: 'DEBYE'},
    {label: 'Pitzer', value: 'PITZER'},
  ]

  reactionsDisplay$: Observable<string> = this.pyequionStore.reactionsLatex$.pipe(
    map(this.mergeLatexReactions())
  )

  solidReactionsDisplay$: Observable<string> = this.pyequionStore.solidReactionsLatex$.pipe(
    map(this.mergeLatexReactions())
  )

  speciesDisplay$: Observable<any[]> = this.pyequionStore.solutionResult$.pipe(
    map(solRst => {
      if (!solRst)
        return null
      const collection = solRst.specie_names.map((v,i) => {
        return {
          name: v,
          conc: solRst.c_molal[i]
        }
      }).filter(val => val.name !== 'H2O' && (!val.name.includes('g')))
      return collection
    })
  )

  precipitatedDisplay$: Observable<any[]> = this.pyequionStore.solutionResult$.pipe(
    map(solRst => {
      if (!solRst)
        return null
      const greaterZero = Object.keys(solRst.preciptation_conc).filter(key => {
        return solRst.preciptation_conc[key] > 1e-20
      })
      return greaterZero
    }),
    filter(names => {
      if (!names)
        return null
      return(!!names.length)
    }
  ))

  constructor(
    private fb: FormBuilder,
    private pyequionStore: PyequionStoreService,
    ) {}

  private mergeLatexReactions(): (value: any[], index: number) => string {
    return reactions => {
      if (!reactions)
        return null;
      let allReactions = '';
      reactions.forEach(v => {
        allReactions += `${v} \\\\`;
      });
      return allReactions;
    };
  }

  ngOnInit() {
    // this.firstFormGroup = this.fb.group({
    //   firstCtrl: ['', Validators.required]
    // });
    this.compoundsInputForm = this.fb.group({
      compounds: this.fb.array([
        this.fb.control('', Validators.required)
      ],
      // [Validators.required]
      ),
      closingEqType: [mapCloseEquationEnum['NONE'], Validators.required], //this.fb.control('NONE', Validators.required)
      initial_feed_mass_balance: [''],
    })

    this.equilibriumCalcForm = this.fb.group({
      // secondCtrl: ['', Validators.required],
      concentrations: this.fb.array([
        this.fb.control(1.0, Validators.required)
      ]),
      temperature: [25.0, Validators.required],
      extraParameter: [null],
      allowPrecipitation: [false, Validators.required],
      nonidealityType: ['DEBYE', Validators.required],
    });

    this.pyequionStore.eqSystem$.subscribe((eqSys: EquilibriumModel) => {
      // if (!eqSys)
      //   return
      const extraPrmtrCtrl = this.equilibriumCalcForm.get('extraParameter')
      const closeVal = this.compoundsInputForm.get('closingEqType').value
      const concVals = this.equilibriumCalcForm.get('concentrations').value
      const compoundsVals = this.compoundsInput.value
      if (mapCloseEquationInverse[closeVal] == 'OPEN')
        extraPrmtrCtrl.setValue(pCO2_ref)
      else if (mapCloseEquationInverse[closeVal] == 'PH')
        extraPrmtrCtrl.setValue(7.0)
      else if (mapCloseEquationInverse[closeVal] == 'CARBON_TOTAL') {
        const idxCarbon = concVals.map((v,i) => {
          const splt = compoundsVals[i].split(/(?=[A-Z])/)
          const ck = splt.includes('C')
          return ck ? i : undefined
        }).filter(x => x !== undefined)

        if (concVals) {
          let sm = 0.0
          for (let index = 0; index < concVals.length; index++) {
            const conc = concVals[index];
            if (idxCarbon.includes(index))
              sm += conc
          }
          extraPrmtrCtrl.setValue(sm)
        }
        console.log('depois')
      }
    })

    // this.pyequionStore.solutionResult$.subscribe((solRslt: any) => {
    //   console.log('HIT AQUI');
    //   if (!solRslt)
    //     return
    //   const idxH2O = solRslt.specie_names.findIndex(v => v === 'H2O')//[0]
    //   // this.indexSpeciesShow = Array(solRslt.specie_names.length-1).fill(0).map((v,i) => {
    //   //   return i === idxH2O ?
    //   // })

    //   solRslt.specie_names.splice(idxH2O,1)
    //   solRslt.c_molal.splice(idxH2O,1)
    //   // this.indexSpeciesShow = []
    //   // // for (const iterator of solRslt.specie_names) {
    //   //   for (let index = 0; index < solRslt.specie_names.length; index++) {
    //   //     if ()
    //   //   this.indexSpeciesShow.push()
    //   //   }
    // })

  }

  get compoundsInput() {
    return this.compoundsInputForm.get('compounds') as FormArray;
  }

  get concentrations() {
    return this.equilibriumCalcForm.get('concentrations') as FormArray;
  }

  get closingEqType() {
    // console.log(this.compoundsInputForm.get('compounds').value)
    return this.compoundsInputForm.get('closingEqType'); //as FormControl
  }

  handleMoreInputCompound() {
    this.compoundsInput.push(this.fb.control('', Validators.required));
    this.concentrations.push(this.fb.control(1.0, Validators.required));
  }

  handleRemoveCompound(i: number) {
    if (!this.compoundsInput.length)
      return
    if (this.compoundsInput.length == 1) {
      this.compoundsInput.controls[i].setValue('')
      return
    }
    if ((i+1) === this.compoundsInput.length) {
      this.compoundsInput.controls[i].setValue('')
    }
    this.compoundsInput.controls.splice(i, 1)
    this.concentrations.controls.splice(i, 1)
    // this.compoundsInput.push(this.fb.control('', Validators.required));
    // this.concentrations.push(this.fb.control(1.0, Validators.required));
  }

  handleCreateEquilibrium() {
    this.pyequionStore.emitValueEqSystem(null)
    let initialFromFeed: string[] = null
    if (this.compoundsInputForm.value.initial_feed_mass_balance.trim() != '')
      initialFromFeed = this.compoundsInputForm.value.initial_feed_mass_balance.split(';')
    const compInput = {
      ...this.compoundsInputForm.value,
      initial_feed_mass_balance: initialFromFeed,
    }
    this.pyequionStore.createEquilibrium(compInput)
  }

  handleCalculateEquilibrium() {
    this.pyequionStore.emitValueSolutionResults(null)
    const calcEqInputsFromForm = this.equilibriumCalcForm.value
    const sysEqSerialized = this.pyequionStore.getValueEqSystem().sys_eq
    const inputToEqCalc = {
      ...calcEqInputsFromForm,
      sys_eq: sysEqSerialized
    }
    this.pyequionStore.calculateEquilibrium(inputToEqCalc)
    console.log('aow')
  }

  ngOnDestroy() {
    // this.eqSystemSubject.unsubscribe()
  }
}
